@import url('https://fonts.googleapis.com/css2?family=Inconsolata&family=Karla:wght@500&display=swap');

html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}
*, ::after, ::before {
  box-sizing: inherit;
}
body {
  margin: 0;
  background-color: #ffffff;
  font-family: 'Inconsolata', monospace;
  font-size: 1.8rem;
  line-height: 1.5;
  font-weight: 400;
  color: #343330;
}
.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 30px;
  padding-left: 30px;
  width: 100%;
  max-width: 1400px;
}
.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}
.col {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}
.col-2 {
  flex: 0 0 16.66%;
  max-width: 16.66%;
}
.col-10 {
  flex: 0 0 83.33%;
  max-width: 83.33%;
}
@media screen and (max-width: 768px) {
  .col {
    margin-bottom: 30px;
    flex: 0 0 100%;
    max-width: 100%;
  }
}

/* Type */
h1, h2, h3 {
  font-family: 'Karla', sans-serif;
  font-weight: 500;
  line-height: 1.2;
}
h1 {
  margin: 15rem 0 5rem;
  font-size: 6rem;
}
h2 {
  margin: 5rem 0 1.5rem;
  font-size: 3rem;
}
p {
  margin: 1.2rem 0;
}
a {
  color: #222323;
  text-decoration: none;
  box-shadow: inset 0 -5px 0 0 rgba(54, 70, 82, 0.3);
  transition: box-shadow 0.2s ease-out;
}
a:hover, a:focus {
  box-shadow: inset 0 -18px 0 0 rgba(54, 70, 82, 0.3);
  transition-timing-function: ease-in;
}
@media screen and (max-width: 768px) {
  h1 {
    margin-top: 6rem;
    font-size: 4rem;
  }
  h2 {
    margin-top: 4rem;
    font-size: 3rem;
  }
}

.intro {
  max-width: 900px;
}

.sticky-container {
  display: flex;
  height: 100%;
}
.sticky {
  align-self: flex-end;
  bottom: 2rem;
  position: -webkit-sticky;
  position: sticky;
}

/* Legend */
.language-legend {
  height: 200px;
  background-image: url("../assets/language_legend.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

/* Sources */
.source {
  padding: 50px 0 30px;
  font-size: 1.6rem;
}
.source ul {
  margin: 0;
  padding-left: 25px;
  list-style: circle;
}
.source li {
  margin: 5px 0;
}